body {
  display: flex;
  position: relative;
  min-width: 320px;
  font-family: $text;
  font-weight: 400;
  background-color: $site-bg;
  color: $text-c;
  font-style: normal;
  font-size: 16px;
  overflow-x: hidden;
  padding-top: 100px;
  @include media-breakpoint-down(sm) {
    padding-top: 70px; }
  .preloader-wr {
    width: 100%;
    height: 100%;
    position: fixed;
    left: 0;
    top: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
    background-color: #2A2523;
    .text-primary {
      color: lighten(#2A2523, 15) !important; } }
  &:not(.home) {
    .preloader-wr {
      display: none; } }
  &.overflow {
    overflow: hidden;
    width: 100%;
    height: 100%; }
  #page {
    display: flex;
    min-height: 100vh;
    width: 100%;
    flex-direction: column;
    // @media (min-width: 2000px)
    //   min-height: 1500px
    main {
      flex-grow: 1; } } }


